.product {
	width: 100%;
	min-width: 330px;
	height: 120px;
	margin: 10px;
	max-width: 430px;
	position: relative;
	min-height: 120px;
	max-height: 120px;
}

.product__container {
	background: #eee;
	padding: 10px;
	display: flex;
	justify-content: space-between;
}

.product__left-section {
	flex: 0.7;
	min-width: 100px;
	line-height: 1;
	font-weight: 600;
}

.product__title {
	letter-spacing: 1px;
	font-size: x-large;
	margin: 0;
	text-transform: capitalize;
}

.product__details {
	letter-spacing: 0.7px;
	margin: 4px 0 0 0;
	font-weight: 500;
	font-size: small;
	font-family: "Montserrat";
}

.product__price {
	letter-spacing: 0.5px;
	font-size: large;
	margin-top: 20px;
}

.product__right-section {
	flex: 0.2;
	width: 100%;
	background-color: #ddd;
	border: none;
	min-width: 100px;
	height: 100px;
}

.product__image {
	width: 100%;
	height: 100%;
	min-height: 100%;
	max-height: 100%;
	max-width: 100%;
	object-fit: cover;
	position: relative;
	border: none;
}

.product__add-button {
	width: 30px;
	height: 27px;
	position: absolute;
	right: 10px;
	bottom: 10px;
	border: none;
	background: rgb(248, 10, 111);
	cursor: pointer;
}

.addIcon {
	position: absolute;
	left: 50%;
	top: 50%;
	transform: translate(-50%, -50%);
	color: white;
}

@media only screen and (max-width: 1070px) {
	.product__title {
		letter-spacing: 0.5px;
		font-size: 1.1rem;
	}
}

@media only screen and (max-width: 1070px) {
	.product__details {
		letter-spacing: 0;
	}
}
